.special-offers {
	margin: 44px 0 47px;
	display: flex;
	flex-direction: column;
	max-width: 1436px;
	margin-left: auto;
	width: 100%;
	margin-right: auto;
	// overflow: hidden;
	position: relative;

	.container {
		display: flex;
		flex-wrap: wrap;
		position: relative;
	}

	&__title {
		font-family: "HelveticaNeueCyr-Light";
		font-size: 30px;
		font-weight: 400;
		line-height: 36px;
		margin-bottom: 38px;
		white-space: nowrap;
		width: 100%;
		padding-left: 4px;
	}

	&__slide {
		background-color: #ffffff;
		display: flex;
		flex-direction: column;
		padding: 20px 18px 14px;
		justify-content: flex-start;
		align-content: stretch;
		border: 1px solid #f1f1f1;
		// min-height: 445px;
		box-shadow: 0 2px 21px 0 transparent;
		position: relative;

		&:hover {
			box-shadow: 0 2px 21px rgba(0, 0, 0, .1);
			// border: 1px solid transparent;
		}
	}

	&__text {
		font-family: "HelveticaNeueCyr-Roman";
		font-size: 18px;
		font-weight: 400;
		line-height: 26px;
		color: black;
		padding: 7px 0 17px;
		display: block;
		flex-shrink: 0;
		text-decoration: unset !important;

		p {
			color: #4b4b4b;
			font-family: "HelveticaNeueCyr-Roman";
			font-size: 13px;
			font-weight: 400;
			line-height: 20px;
			margin-bottom: 2px;
		}

		&:hover {
			color: $base;
		}
	}

	&__sale {
		display: flex;
		justify-content: space-between;
		width: 100%;
		margin-top: auto;
		flex-wrap: wrap;
		// align-items: center;
		flex-shrink: 0;
		flex-grow: 0;
		align-items: center;
		margin-bottom: 8px;
	}

	&__price {
		font-family: "HelveticaNeueCyr-Medium";
		font-size: 18px;
		font-weight: 400;
		line-height: 22px;
		color: black;
		position: relative;
		display: flex;
		align-items: baseline;

		// @media screen and (max-width: 1128px) {
		//     font-size: 18px;
		//     flex-shrink: 0;
		// }
		// @media screen and (max-width:1100px) {
		//     font-size: 17px;
		//     flex-shrink: 0;
		// }
		&.price__dashed {
			position: relative;
			margin-top: auto !important;
			margin-bottom: 0 !important;
			padding-right: 3px;
			height: 36px !important;
			flex-grow: 0;
			color: $base;
			flex-wrap: wrap;
			// width: 50%;
			height: 100%;
			align-items: flex-start !important;
			position: relative;

			.catalog__price {
				width: calc(100% - 112px);
				height: 100%;
				align-items: flex-start;
				padding-top: 2px;
				line-height: 1;
			}

			.special-offers__rub {
				line-height: 1;
			}

			.catalog__price {
				color: $base;
				flex-wrap: wrap;
				// width: 50%;
				height: 100%;
				align-items: flex-start;
				position: relative;
			}

			.price__absolute {
				position: absolute;
				left: 0;
				color: #a6a6a6;
				font-size: 11px;
				font-weight: 400;
				line-height: 15px;
				// text-decoration: line-through;
				left: 0;
				top: unset;
				bottom: 0;
			}

			+.special-offers__buy {
				margin-top: auto;
			}
		}

		.price__absolute {
			position: absolute;
			color: #a6a6a6;
			font-size: 11px;
			font-weight: 400;
			line-height: 15px;
			// text-decoration: line-through;
			left: 0;
			top: -20px;
			white-space: nowrap;

			&:before {
				content: "";
				width: calc(100% - 24px);
				position: absolute;
				height: 1px;
				background: $base;
				transform: rotate(-13deg);
				bottom: 6px;
			}
		}
	}

	&__rub {
		font-family: "HelveticaNeueCyr-Medium";
		font-size: 18px;
		font-weight: 400;
		line-height: 22px;
		margin-left: 5px;
		font-style: normal;
	}

	&__buy {
		width: 32px;
		height: 30px;
		font-size: 0;
		background: url("../images/buy02.svg");

		// @media screen and (max-width: 1128px) {
		//     font-size: 11px;
		//     padding: 0;
		//     width: 39%;
		//     text-align: center;
		//     justify-content: center;
		// }
		&:before {
			display: none;
		}
	}

	&__pic {
		width: 100%;
		max-height: 250px;
		height: 100%;
		align-items: center;
		display: flex;
		justify-content: center;

		img {
			width: auto;
		}
	}

	.swiper-scrollbar {
		display: none;
	}

	.swiper-pagination,
	.swiper-pagination_2,
	.swiper-pagination_3 {
		top: 23px;
		right: 124px;
		display: flex;
		justify-content: flex-end;
	}

	.swiper-pagination-bullet {
		margin-left: 16px;
	}

	&.new-items {
		margin-bottom: 46px;

		.swiper-button-prev,
		.swiper-button-next {
			top: 21px;
		}

		.swiper-pagination,
		.swiper-pagination_2,
		.swiper-pagination_3 {
			top: 14px;
		}
	}

	.swiper-button-prev,
	.new-items_prev {
		background: url("../images/special-offers_prev.svg") 0/34px no-repeat;
		position: absolute;
		top: 30px;
		right: 48px;
		left: unset;
		width: 34px;
		height: 34px;
		// &:hover {
		// 	background: url("../images/swiper-button-color-prev.svg") 0/34px no-repeat;
		// }
	}

	.new-items_prev {
		background: url("../images/new-items_prev.png") 0/34px no-repeat;
	}

	.swiper-button-next,
	.new-items_next {
		background: url("../images/special-offers_next.svg") 0/34px no-repeat;
		top: 30px;
		right: 3px;
		width: 34px;
		height: 34px;
		// &:hover {
		// 	background: url("../images/swiper-button-color-next.svg") 0/34px no-repeat;
		// }
	}

	.new-items_next {
		background: url("../images/new-items_next.png") 0/34px no-repeat;
	}

	.swiper-pagination-bullet {
		background: #000 !important;

		&-active {
			background: $base !important;
			box-sizing: border-box;
		}
	}

	&.promotional-goods {
		margin-top: 0;
		margin-bottom: 47px;

		.swiper-pagination {
			top: 14px;
			right: 124px;
			display: flex;
			// justify-content: flex-end;
		}

		.price__dashed {
			position: relative;
			margin-top: auto !important;
			margin-bottom: 8px;
			padding-right: 3px;
			height: 36px;
			align-items: flex-end;
			flex-grow: 0;

			.catalog__price {
				width: calc(100% - 112px);
				height: 100%;
				align-items: flex-start;
				padding-top: 2px;
				line-height: 1;
			}

			.special-offers__rub {
				line-height: 1;
			}

			.catalog__price {
				color: $base;
				flex-wrap: wrap;
				// width: 50%;
				height: 100%;
				align-items: flex-start;
				position: relative;
			}

			.price__absolute {
				position: absolute;
				left: 0;
				color: #999999;
				font-size: 13px;
				font-weight: 400;
				line-height: 1;
				// text-decoration: line-through;
				left: 0;
				top: unset;
				bottom: 0;
			}
		}
	}

	.swiper-container {
		padding: 20px;
		margin: -20px;
		width: calc(100%+40px);
	}

	&__content {
		display: flex;
		flex-direction: column;
	}

	.swiper {
		display: flex;
		// position: relative;
		margin-bottom: 14px;
		max-width: 100%;

		* {
			transition: 0s !important;
		}

		.swiper {
			&-slide {
				height: 228px;
				opacity: 0 !important;
				position: unset;
				transform: unset !important;

				&-active {
					opacity: 1 !important;
					order: -1;

					&.swiper-slide-text {
						// position: absolute;
						// top: 0;
						// left: 0;
						// bottom: 64px;
						// background: white;
						transform: unset !important;

						.special-offers__new {
							&:hover {}

							&:active {}

							&:focus {}
						}
					}
				}
			}

			&-pagination {
				height: 250px;
				display: flex;
				width: auto;
				left: 8px;
				right: 8px;
				top: 4px;

				&-bullet {
					flex-grow: 1;
					opacity: 1;
					border-radius: unset;
					margin-left: unset;
					width: auto;
					height: 100%;
					background: unset !important;
					display: flex;
					opacity: 0;

					&:not(:last-of-type) {
						margin-right: 4px;
					}

					&:before {
						display: flex;
						content: "";
						height: 1px;
						background: #ececec;
						width: 100%;
					}

					&-active {
						&:before {
							background: #4b4b4b;
						}
					}
				}
			}

			&-wrapper {
				transform: unset !important;
			}

			&-wrapper,
			&-container {
				position: unset;
			}

			&-container {
				padding: 0;
				margin: 0;
				width: 100%;
			}
		}
	}

	.announcing {
		width: 100%;
		background-size: cover;
		background-position: -4px 0;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;

		&__item {
			display: block;
			// height: 24px;
			margin-right: 8px;

			img {
				width: 24px;
				height: 24px;
				border-radius: 50%;
				overflow: hidden;
			}
		}

		&__status {
			width: 100%;
			display: flex;
			font-family: "HelveticaNeueCyr-Medium";
			font-size: 14px;
			font-weight: 400;
			line-height: 23px;
			margin-top: 8px;

			span {
				color: #31b64b;
			}
		}
	}

	.ratings {
		display: flex;
		margin-left: auto;
		align-items: center;

		&__star {
			width: 16px;
			height: 16px;
			background: url(../images/star.svg) no-repeat;
			background-position: 50%;
			background-size: 16px 16px;

			&.ratings__none {
				background: url(../images/rectangle-2.svg) no-repeat;
				background-position: 50%;
				background-size: 16px 16px;
			}
		}

		&__number {
			font-family: "HelveticaNeueCyr-Bold";
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;
			margin: 0 2px 0 8px;
		}

		&__link {
			font-family: "HelveticaNeueCyr-Roman";
			font-size: 14px;
			font-weight: 400;
			line-height: 24px;
		}
	}

	&__fav {
		width: 30px;
		height: 30px;
		background: url("../images/fav.svg");
		cursor: pointer;

		&:hover {
			background: url("../images/fav-c.svg");
		}

		&.active {
			background: url("../images/fav-a.svg");
		}
	}

	&__compare {
		width: 30px;
		height: 30px;
		background: url("../images/com.svg");
		cursor: pointer;

		&:hover {
			background: url("../images/com-c.svg");
		}
	}

	&__new {
		position: absolute;
		top: 20px;
		left: 18px;
		right: 18px;
		right: 18px;
		bottom: 64px;
		background: white;
		text-decoration: unset;

		&:hover {
			text-decoration: unset;
		}

		&:active {}

		&:focus {}
	}

	&__caption {
		font-family: "HelveticaNeueCyr-Roman";
		font-size: 18px;
		font-weight: 400;
		line-height: 26px;
		margin-bottom: 8px;
		margin-top: 4px;
	}

	&__desc {
		display: flex;
		flex-direction: column;

		ul {
			list-style: none;
			padding-left: unset;
			margin-bottom: 4px;
		}

		li {
			color: #4b4b4b;
			font-family: "HelveticaNeueCyr-Roman";
			font-size: 12px;
			font-weight: 400;
			line-height: 14px;
			margin-bottom: 8px;
			display: flex;
			align-items: flex-end;

			b {
				font-family: "HelveticaNeueCyr-Bold";
				width: 130px;
				margin-left: auto;
				text-align: right;
				padding-left: 16px;
				flex-shrink: 0;
			}
		}
	}

	&__info {
		font-family: "HelveticaNeueCyr-Roman";
		font-size: 14px;
		font-weight: 400;
		line-height: 20px;
		margin-top: 13px;
		color: black;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 4;
		overflow: hidden;
	}

	&__fav,
	&__compare,
	&__buy {
		opacity: 0;
		background-repeat: no-repeat;
		background-size: contain !important;
	}

	&__slide:hover,
	.catalog__item:hover {
		.special-offers {

			&__fav,
			&__compare,
			&__buy {
				opacity: 1;
			}
		}

		.swiper-pagination-bullet {
			opacity: 1;
		}
	}

	@media screen and (max-width: 767px) {
		margin: 27px 0 36px;
		padding-bottom: 24px;

		.announcing {
			top: 15px;
			left: 16px;
		}

		.swiper-button-next {
			right: -1px;
			top: 17px;
		}

		.swiper-button-prev {
			right: 46px;
			top: 17px;
		}

		.swiper-pagination_2 {
			// right: 122px;
			// top: 9px;
		}

		.swiper {
			.swiper-slide {
				opacity: 1 !important;

				&-text {
					position: static;
				}

				.special-offers__new {
					position: static;
				}
			}
		}

		.swiper-pagination-bullet {}

		&__pic {}

		&__title {
			margin-bottom: 19px;
			color: #212121;
			font-size: 24px;
			font-weight: 400;
			white-space: normal;
			line-height: 34px;
			padding-left: 0;
		}

		.new-items_prev,
		.swiper-button-prev,
		.new-items_next,
		.swiper-button-next {
			display: none;
		}

		.new-items__link {
			width: 100%;
			text-align: center;
		}

		.swiper-button-prev,
		.swiper-button-next {
			display: none !important;
		}

		.special-offers__buy {
			color: #ffffff;
			// background-image: none;
			// width: 92px;
			// height: 36px;
			display: flex;
			justify-content: center;
		}

		.announcing {
			top: 20px;
			left: 20px;

			&__status {
				width: auto;
				margin-top: 0;
				margin-left: auto;

				span {
					display: none;
				}
			}
		}

		.special-offers__buy,
		.special-offers__compare,
		.special-offers__fav {
			opacity: 1;
			background-repeat: no-repeat;
		}

		.special-offers__slide {
			padding: 15px 19px 12px;
			min-height: unset;
		}

		.special-offers__sale {
			align-items: center;
			justify-content: unset;
		}

		.special-offers__fav {
			margin-left: auto;
			background-size: 24px;
		}

		.special-offers__compare {
			margin: 0 20px 0 7px;
			background-size: 24px;
		}

		.special-offers__pic {
			max-height: 240px;
		}

		.special-offers__text {
			font-size: 18px;
			padding: 19px 0 37px;

			span {
				display: block;
			}
		}

		.special-offers__content {
			padding-right: 4px;
		}

		.swiper-pagination_2,
		.swiper-pagination {
			display: flex;
			bottom: -28px;
			width: 100%;
			margin: 0 auto;
			justify-content: center;
			left: 0;
			top: unset;

			* {
				margin: 0 5px;
			}
		}

		.ratings {
			&__star {
				width: 21px;
				height: 21px;
				background-size: 21px;
			}

			&__number {
				margin-left: 6px;
			}
		}

		&.promotional-goods {
			margin-bottom: 31px;

			.special-offers__text {
				padding-bottom: 39px;
			}

			.special-offers__sale {
				flex-grow: 0;
				margin-top: auto;
			}

			.swiper-pagination {
				position: unset;
				margin: 28px auto 0;
			}

			.special-offers__slide {
				padding: 30px 21px 15px;
			}
		}

		.swiper {
			.swiper {
				&-pagination {
					height: auto;
					bottom: unset;
				}
			}
		}
	}
}
